div#header {
  background: #202020;
  padding: 12px;
}

input#url {
  background: #121212;
  border: 1px solid #303030;
  color: #ffffff;
  font-size: 16px;
  line-height: 24px;
  padding: 3px 7px;
  width: 40%;
}

button#search {
  background: #ff0000;
  border: 1px solid #303030;
  color: white;
  cursor: pointer;
  font-size: 16px;
  line-height: 24px;
  padding: 4px;
  width: 64px;
}

button#search:hover {
  background: #dd0000;
}
